.feedback {
  padding: 3rem 0;
  background: linear-gradient(135deg, #f8f9fa 0%, #ffffff 100%);
  margin-left:auto; margin-right:auto;
}

.feedback-header {
  text-align: center;
  margin-bottom: 2rem;
}

.feedback-gallery-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
  position: relative;
}

.feedback-gallery {
  display: flex;
  gap: 1rem;
  overflow-x: auto;
  padding: 1rem 0;
  scrollbar-width: thin;
  scrollbar-color: #d4af37 #f0f0f0;
  align-items: center;
  justify-content: center;
}

.feedback-gallery::-webkit-scrollbar {
  height: 8px;
}

.feedback-gallery::-webkit-scrollbar-track {
  background: #f0f0f0;
  border-radius: 4px;
}

.feedback-gallery::-webkit-scrollbar-thumb {
  background: #d4af37;
  border-radius: 4px;
}

.feedback-gallery::-webkit-scrollbar-thumb:hover {
  background: #b8941f;
}

.feedback-item {
  flex: 0 0 auto;
  width: 200px;
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
  cursor: pointer;
  border: 1px solid #f0f0f0;
}

.feedback-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.12);
}

.feedback-image {
  width: 100%;
  height: 150px;
  object-fit: cover;
  display: block;
  transition: transform 0.3s ease;
}

.feedback-item:hover .feedback-image {
  transform: scale(1.05);
}

/* Кнопки навигации */
.gallery-nav {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(212, 175, 55, 0.9);
  color: white;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 1.2rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
}

.gallery-nav:hover {
  background: #b8941f;
  transform: translateY(-50%) scale(1.1);
}

.gallery-nav.prev {
  left: 0.5rem;
}

.gallery-nav.next {
  right: 0.5rem;
}

.gallery-nav.hidden {
  display: none;
}

/* Модальное окно для просмотра фото */
.feedback-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.95);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 2rem;
  backdrop-filter: blur(5px);
}

.feedback-modal {
  background: white;
  border-radius: 15px;
  max-width: 90vw;
  max-height: 90vh;
  overflow: hidden;
  position: relative;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  animation: modalAppear 0.3s ease-out;
}

@keyframes modalAppear {
  from {
    opacity: 0;
    transform: scale(0.9) translateY(20px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.modal-close-btn {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 1.5rem;
  cursor: pointer;
  z-index: 10;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-close-btn:hover {
  background: #e74c3c;
  transform: scale(1.1);
}

.modal-image {
  max-width: 100%;
  max-height: 80vh;
  object-fit: contain;
  display: block;
}

/* Адаптивность */
@media (max-width: 768px) {
  .feedback {
    padding: 2rem 0;
  }

  .feedback-item {
    width: 180px;
  }

  .feedback-image {
    height: 130px;
  }

  .gallery-nav {
    width: 35px;
    height: 35px;
    font-size: 1rem;
  }

  .gallery-nav.prev {
    left: 0.2rem;
  }

  .gallery-nav.next {
    right: 0.2rem;
  }

  .feedback-modal-overlay {
    padding: 1rem;
  }
}

@media (max-width: 480px) {
  .feedback-header {
    margin-bottom: 1.5rem;
  }

  .feedback-item {
    width: 160px;
  }

  .feedback-image {
    height: 120px;
  }

  .feedback-gallery {
    gap: 0.8rem;
  }

  .gallery-nav {
    display: none;
  }
}