.contacts {
  padding: 5rem 0;
  background: linear-gradient(135deg, #ffffff 0%, #f8f9fa 100%);
}

.contacts-header {
  text-align: center;
  margin-bottom: 4rem;
}

.contacts-content {
  max-width: 1200px;
  margin: 0 auto;
}

/* Контактная информация */
.contact-info-section {
  margin-bottom: 4rem;
}

.contact-info-section h3 {
  font-size: 1.8rem;
  color: #333;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 600;
}

.contact-info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
}

.contact-info-card {
  background: white;
  padding: 2.5rem 2rem;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  text-align: center;
  transition: all 0.3s ease;
  border: 1px solid #f0f0f0;
}

.contact-info-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.12);
}

.contact-icon {
  font-size: 3rem;
  margin-bottom: 1.5rem;
}

.contact-info-card h4 {
  font-size: 1.3rem;
  color: #333;
  margin-bottom: 1rem;
  font-weight: 600;
}

.contact-details {
  margin-bottom: 1rem;
}

.contact-detail {
  color: black;
  font-weight: 500;
  margin-bottom: 0.5rem;
  font-size: 1.05rem;
}

.contact-description {
  color: #666;
  font-size: 0.95rem;
  line-height: 1.5;
}

/* Основной контент - форма и карта */
.contacts-main {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  margin-bottom: 5rem;
}

/* Форма обратной связи */
.contact-form-section {
  background: white;
  padding: 3rem;
  border-radius: 20px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.08);
}

.contact-form-section h3 {
  font-size: 1.8rem;
  color: #333;
  margin-bottom: 2rem;
  font-weight: 600;
}

.success-message {
  background: #d4edda;
  color: #155724;
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 2rem;
  border: 1px solid #c3e6cb;
  font-weight: 500;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-weight: 500;
  color: #333;
  margin-bottom: 0.5rem;
  font-size: 0.95rem;
}

.form-group input,
.form-group select,
.form-group textarea {
  padding: 1rem;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
  font-family: inherit;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #d4af37;
  box-shadow: 0 0 0 3px rgba(212, 175, 55, 0.1);
}

.form-group textarea {
  resize: vertical;
  min-height: 120px;
}

.submit-btn {
  background: black;
  color: white;
  border: none;
  padding: 1.2rem 2rem;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 1rem;
}

.submit-btn:hover {
  background: gray;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(212, 175, 55, 0.3);
}

/* Секция с картой и соцсетями */
.contact-map-section {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.map-container {
  background: white;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
}

.map-placeholder {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  position: relative;
}

.map-content {
  text-align: center;
  z-index: 2;
}

.map-content h4 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  font-weight: 600;
}

.map-content p {
  margin-bottom: 2rem;
  opacity: 0.9;
}

.map-actions {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
}

.map-btn {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 0.8rem 1.5rem;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  font-size: 0.9rem;
}

.map-btn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-2px);
}

/* Социальные сети */
.social-section {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
}

.social-section h4 {
  font-size: 1.3rem;
  color: #333;
  margin-bottom: 1.5rem;
  text-align: center;
  font-weight: 600;
}

.social-links {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
}

.social-link {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  padding: 4rem 4rem;      /* длиннее по горизонтали, ниже по вертикали */
  border-radius: 50px;       /* большое значение даёт вытянутую форму */
  text-decoration: none;
  color: #333;
  transition: all 0.3s ease;
  border: 1px solid #f0f0f0;
  font-weight: 500;
  background-color: #fff;

  width: fit-content;         /* тянется под текст */
  white-space: nowrap;
  overflow: visible;
}


.social-link:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.social-link.instagram:hover {
  background: linear-gradient(45deg, #f09433, #e6683c, #dc2743, #cc2366, #bc1888);
  color: white;
  border-color: transparent;
}

.social-icon {
  font-size: 1.2rem;
}

/* CTA секция */
.contacts-cta {
  background: linear-gradient(135deg, #2c3e50 0%, #34495e 100%);
  color: white;
  padding: 4rem 2rem;
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}

.cta-content > p {
  font-size: 1.1rem;
  margin-bottom: 2rem;
  opacity: 0.9;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
}

.cta-phone {
  margin-bottom: 1.5rem;
}

.phone-link {
  font-size: 1.3rem;
  font-weight: bold;
  color: black;
  text-decoration: none;
  transition: all 0.3s ease;
  display: inline-block;
  margin-bottom: 0.5rem;
}

.phone-link:hover {
  color: white;
  transform: scale(1.05);
}

.cta-note {
  font-size: 0.9rem;
  opacity: 0.8;
}

/* Адаптивность */
@media (max-width: 968px) {
  .contacts-main {
    grid-template-columns: 1fr;
    gap: 3rem;
  }

  .form-row {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .contacts {
    padding: 3rem 0;
  }

  .contact-form-section {
    padding: 2rem;
  }

  .contact-info-grid {
    grid-template-columns: 1fr;
  }

  .phone-link {
    font-size: 1.2rem;
  }

  .map-actions {
    flex-direction: column;
    align-items: center;
  }

  .map-btn {
    width: 200px;
  }
}

@media (max-width: 480px) {
  .contacts-header {
    margin-bottom: 2rem;
  }

  .contact-info-card {
    padding: 2rem 1.5rem;
  }

  .contact-form-section {
    padding: 1.5rem;
  }

  .contacts-cta {
    padding: 3rem 1.5rem;
  }

  .phone-link {
    font-size: 1.1rem;
  }

  .contact-icon {
    font-size: 2.5rem;
  }
}