.featured-products {
  padding: 5rem 0;
  background: #f8f9fa;
}

.carousel-container {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.carousel {
  position: relative;
  display: flex;
  align-items: center;
  background: white;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.carousel-track-container {
  overflow: hidden;
  flex: 1;
  margin: 0 1rem;
}

.carousel-track {
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.carousel-slide {
  min-width: 100%;
  padding: 2rem;
  box-sizing: border-box;
}

.product-card {
  display: flex;
  align-items: center;
  gap: 3rem;
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.15);
}

.product-image-container {
  flex: 0 0 300px;
  position: relative;
}

.product-image {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 10px;
  transition: transform 0.3s ease;
}

.product-card:hover .product-image {
  transform: scale(1.05);
}

.product-info {
  flex: 1;
  text-align: left;
}

.product-badge {
  position: absolute;
  top: 1rem;
  left: 1rem;
  background: black;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  z-index: 2;
}

.product-title {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: #333;
}

.product-description {
  color: #666;
  font-size: 1rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.product-price {
  color: black;
  font-weight: bold;
  font-size: 1.5rem;
  margin-bottom: 2rem;
}

.add-to-cart {
  background: #333;
  color: #fff;
  border: none;
  padding: 1rem 2rem;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 500;
  font-size: 1rem;
  min-width: 150px;
}

.add-to-cart:hover {
  background: black;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(212, 175, 55, 0.3);
}

.carousel-button {
  background: black;
  color: white;
  border: none;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  font-size: 1.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
}

.carousel-button:hover {
  background: gray;
  transform: scale(1.1);
}

.carousel-indicators {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 2rem;
}

.indicator {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: none;
  background: #ddd;
  cursor: pointer;
  transition: all 0.3s ease;
}

.indicator.active {
  background: black;
  transform: scale(1.2);
}

.indicator:hover {
  background: #b8941f;
}

/* Адаптивность */
@media (max-width: 968px) {
  .product-card {
    flex-direction: column;
    text-align: center;
    gap: 2rem;
  }

  .product-image-container {
    flex: 0 0 250px;
  }

  .product-image {
    height: 250px;
  }

  .product-info {
    text-align: center;
  }
}

@media (max-width: 768px) {
  .carousel-container {
    padding: 0 1rem;
  }

  .carousel-slide {
    padding: 1rem;
  }

  .product-card {
    padding: 1.5rem;
  }

  .product-title {
    font-size: 1.5rem;
  }

  .carousel-button {
    width: 40px;
    height: 40px;
    font-size: 1.2rem;
  }

  .carousel-track-container {
    margin: 0 0.5rem;
  }
}

@media (max-width: 480px) {
  .featured-products {
    padding: 3rem 0;
  }

  .product-image-container {
    flex: 0 0 200px;
  }

  .product-image {
    height: 200px;
  }

  .product-title {
    font-size: 1.3rem;
  }

  .product-price {
    font-size: 1.3rem;
  }

  .add-to-cart {
    padding: 0.8rem 1.5rem;
    font-size: 0.9rem;
  }
}
.view-details-btn {
  background: #d4af37;
  color: white;
  border: none;
  padding: 0.8rem 1.5rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 500;
  width: 100%;
  margin-top: 1rem;
}

.view-details-btn:hover {
  background: #b8941f;
  transform: translateY(-2px);
}

.product-image-container {
  cursor: pointer;
}