.shop {
  padding: 5rem 0;
  background: linear-gradient(135deg, #f8f9fa 0%, #ffffff 100%);
}

.shop-header {
  text-align: center;
  margin-bottom: 3rem;
}

.shop-content {
  max-width: 1200px;
  margin: 0 auto;
}

/* Основной контент магазина */
.shop-main {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
}

.shop-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #f0f0f0;
}

.products-count {
  font-weight: 500;
  color: #666;
}

.sort-options {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.sort-options label {
  font-weight: 500;
  color: #666;
}

.sort-select {
  padding: 0.5rem 1rem;
  border: 1px solid #e0e0e0;
  border-radius: 5px;
  background: white;
  cursor: pointer;
}

.sort-select:focus {
  outline: none;
  border-color: #d4af37;
}

/* Сетка товаров */
.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.product-card {
  background: #fff;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
  position: relative;
  cursor: pointer;
  display: flex;
  flex-direction: column;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.15);
}

/* УВЕЛИЧИЛИ РАЗМЕР ИЗОБРАЖЕНИЯ */
.product-image {
  position: relative;
  height: 280px; /* Увеличили высоту изображения */
  overflow: hidden;
  flex-shrink: 0; /* Запрещаем сжатие изображения */
}

.product-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.product-card:hover .product-image img {
  transform: scale(1.05);
}

.product-badge {
  position: absolute;
  top: 1rem;
  left: 1rem;
  background: #d4af37;
  color: white;
  padding: 0.4rem 0.8rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  z-index: 2;
}

.product-badge.discount {
  background: #e74c3c;
}

.out-of-stock {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  z-index: 1;
}

/* УМЕНЬШИЛИ ПАДДИНГИ И ОТСТУПЫ В ИНФОРМАЦИИ О ТОВАРЕ */
.product-info {
  padding: 1.2rem; /* Уменьшили паддинг */
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.product-title {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 0.4rem; /* Уменьшили отступ */
  color: #333;
  line-height: 1.3;
}

.product-description {
  color: #666;
  font-size: 0.85rem; /* Немного уменьшили размер шрифта */
  line-height: 1.4;
  margin-bottom: 0.8rem; /* Уменьшили отступ */
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  flex-grow: 1;
}

.product-price {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  margin-bottom: 0.8rem; /* Уменьшили отступ */
  flex-wrap: wrap;
}

.current-price {
  font-size: 1.3rem;
  font-weight: bold;
  color: #d4af37;
}

.original-price {
  font-size: 1rem;
  color: #999;
  text-decoration: line-through;
}

.discount-percent {
  background: #e74c3c;
  color: white;
  padding: 0.2rem 0.5rem;
  border-radius: 12px;
  font-size: 0.8rem;
  font-weight: 600;
}

.product-view-btn {
  color: #d4af37;
  font-weight: 600;
  font-size: 0.9rem;
  transition: all 0.3s ease;
  margin-top: auto; /* Прижимаем кнопку к низу */
}

.product-card:hover .product-view-btn {
  transform: translateX(5px);
}

/* Модальное окно товара */
.product-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 2rem;
  backdrop-filter: blur(5px);
}

.product-modal {
  background: white;
  border-radius: 20px;
  max-width: 900px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  animation: modalAppear 0.3s ease-out;
}

@keyframes modalAppear {
  from {
    opacity: 0;
    transform: scale(0.9) translateY(20px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.modal-close-btn {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 1.5rem;
  cursor: pointer;
  z-index: 10;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-close-btn:hover {
  background: #e74c3c;
  transform: scale(1.1);
}

.modal-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0;
}

.modal-image {
  position: relative;
  height: 500px;
  overflow: hidden;
}

.modal-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.modal-info {
  padding: 2.5rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.modal-info h2 {
  font-size: 1.8rem;
  color: #333;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.modal-description {
  color: #666;
  line-height: 1.6;
  font-size: 1rem;
}

.modal-price {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
  padding: 1rem 0;
  border-top: 1px solid #f0f0f0;
  border-bottom: 1px solid #f0f0f0;
}

.modal-price .current-price {
  font-size: 1.8rem;
}

.modal-price .original-price {
  font-size: 1.2rem;
}

.modal-price .discount-percent {
  font-size: 0.9rem;
  padding: 0.3rem 0.8rem;
}

.product-features h3,
.product-usage h3 {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 1rem;
  font-weight: 600;
}

.product-features ul {
  list-style: none;
  padding: 0;
}

.product-features li {
  padding: 0.5rem 0;
  color: #555;
  border-bottom: 1px solid #f8f8f8;
}

.product-features li:last-child {
  border-bottom: none;
}

.product-usage p {
  color: #666;
  line-height: 1.6;
  background: #f8f9fa;
  padding: 1rem;
  border-radius: 8px;
  border-left: 4px solid #d4af37;
}

.modal-actions {
  display: flex;
  gap: 1rem;
  margin-top: auto;
  padding-top: 2rem;
  border-top: 1px solid #f0f0f0;
}

.contact-btn {
  flex: 2;
  background: #d4af37;
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.contact-btn:hover {
  background: #b8941f;
  transform: translateY(-2px);
}

.close-btn {
  flex: 1;
  background: #f8f9fa;
  color: #333;
  border: 1px solid #e0e0e0;
  padding: 1rem 2rem;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.close-btn:hover {
  background: #e9ecef;
  border-color: #d4af37;
}

/* Адаптивность */
@media (max-width: 768px) {
  .shop {
    padding: 3rem 0;
  }

  .shop-toolbar {
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }

  .products-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 1.5rem;
  }

  .shop-main {
    padding: 1.5rem;
  }

  /* Уменьшаем высоту изображения на мобильных */
  .product-image {
    height: 220px;
  }

  .modal-content {
    grid-template-columns: 1fr;
  }

  .modal-image {
    height: 300px;
  }

  .modal-info {
    padding: 1.5rem;
  }

  .modal-actions {
    flex-direction: column;
  }

  .product-modal-overlay {
    padding: 1rem;
  }
}

@media (max-width: 480px) {
  .shop-header {
    margin-bottom: 2rem;
  }

  .products-grid {
    grid-template-columns: 1fr;
  }

  .sort-options {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }

  /* Еще меньше изображение на очень маленьких экранах */
  .product-image {
    height: 200px;
  }

  .modal-info h2 {
    font-size: 1.5rem;
  }

  .modal-price {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
}