.about {
  padding: 5rem 0;
  background: linear-gradient(135deg, #f8f9fa 0%, #ffffff 100%);
}

.about-header {
  text-align: center;
  margin-bottom: 4rem;
}

.about-content {
  max-width: 1200px;
  margin: 0 auto;
}

/* Основной контент */
.about-main {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: start;
  margin-bottom: 5rem;
}

.about-text h3 {
  font-size: 1.8rem;
  color: #333;
  margin-bottom: 1.5rem;
  font-weight: 600;
}

.about-text p {
  color: #666;
  line-height: 1.7;
  margin-bottom: 1.5rem;
  font-size: 1.05rem;
}

.about-story,
.about-mission,
.about-meaning {
  margin-bottom: 2.5rem;
}

.about-image {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.about-image img {
  width: 100%;
  height: auto;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.about-image img:hover {
  transform: translateY(-5px);
}

/* CTA секция */
.about-cta {
  background: linear-gradient(135deg, black 50%, white 100%);
  color: white;
  padding: 4rem 2rem;
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 20px 40px rgba(212, 175, 55, 0.3);
}

.about-cta h3 {
  font-size: 2.2rem;
  margin-bottom: 1rem;
  font-weight: 600;
}

.about-cta p {
  font-size: 1.1rem;
  margin-bottom: 2.5rem;
  opacity: 0.95;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
}

.cta-buttons {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

.btn {
  display: inline-block;
  padding: 1rem 2.5rem;
  border-radius: 30px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  border: 2px solid transparent;
  cursor: pointer;
}

.btn-primary {
  background: white;
  color: #d4af37;
}

.btn-primary:hover {
  background: transparent;
  color: white;
  border-color: white;
  transform: translateY(-2px);
}

.btn-secondary {
  background: transparent;
  color: white;
  border-color: white;
}

.btn-secondary:hover {
  background: white;
  color: #d4af37;
  transform: translateY(-2px);
}

/* Адаптивность */
@media (max-width: 968px) {
  .about-main {
    grid-template-columns: 1fr;
    gap: 3rem;
  }

  .about-image {
    order: -1;
  }
}

@media (max-width: 768px) {
  .about {
    padding: 3rem 0;
  }

  .about-main {
    gap: 2rem;
    margin-bottom: 3rem;
  }

  .about-text h3 {
    font-size: 1.5rem;
  }

  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }

  .btn {
    width: 200px;
    text-align: center;
  }

  .about-cta {
    padding: 3rem 1.5rem;
  }

  .about-cta h3 {
    font-size: 1.8rem;
  }
}

@media (max-width: 480px) {
  .about-header {
    margin-bottom: 2rem;
  }

  .about-text p {
    font-size: 1rem;
  }

  .about-cta {
    padding: 2.5rem 1rem;
  }

  .about-cta h3 {
    font-size: 1.6rem;
  }
}